.App {
  text-align: center;
}
.App
{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #282c34;
}


.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  height: 2em;
  background-color: #0e1c25;
}
.Body{
  background-color: #0e1c25;
  color: wheat;
  flex: 1;
  margin: 2em;
  display: flex;
  align-items: center;
  flex-direction: column;
  
  /* box-shadow: 0 4px 8px rgba(255, 255, 255, 0.1),
  0 6px 20px rgba(255, 255, 255, 0.2);  */
}
.Submitssion-Container
{
  
 height: 22em;
 width: 65%;
 margin-top: 2em;
 display: flex;
 flex-direction: column;
 align-items: center;
 border: 1px solid rgb(117, 53, 53);

}
.Submitssion-Container label
{
  margin-top: 1em;;
}
.Submitssion-Container input
{
  border: none;
  border-radius: 2px;
  height: 25px;
  width: 50em;
  padding: 3px  ;
}
#message
{
  height: 100px;
  
}
.Submitssion-Container button{
  background-color: red;
  margin-top: 20px;
  margin-bottom: 20px;
  color: white;
  width: 200px;
  height: 55px;
  border: none;
  border-radius: 2px;
}
.post:hover
{
  background-color: rgb(114, 30, 30)
}
.Card-Container
{
  
  
  border: 1px solid rgb(128, 126, 126);
  width: 65%;
  margin-top: 15px;
  margin-bottom: 15px;
  height: auto; 
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  flex-wrap: wrap; 
  justify-content: space-between;

}
.Card
{
  
  background-color: rgb(5 15 29);
  max-width: 45%;
  flex: 0 1 45%;
  display: flex;
  flex-direction: column;
  height: auto;
  margin: 5px;
  padding: 10px;

}
.card-head
{
  font-size: small;
  text-align: end;
  color: rgb(149 212 142);
}
.message
{
  border: 1px solid #322424;
  word-wrap: break-word;
  white-space: normal; 
text-overflow: unset;
padding: 5px;

}
